IG$.MgrManual = $s.extend(IG$.v$pnl, {
	layout: "fit",
	
 	_init_app: function() {
		var me = this,
			t_note_editor = me.down("[name=t_note_editor]"),
			tdiv, editordiv;
			
		setTimeout(function() {
			var summernote = $("#manual_editor_main", t_note_editor.body.dom).summernote({
				tabsize: 4,
				maxHeight: t_note_editor.getHeight() - 80
			});
			
			me._note_editor = summernote;
		}, 100);
		
		me._load_menu();
	},
	
	_load_menu: function() {
		var me = this,
			req = new IG$._rpc$(),
			use_locale = window.useLocale || "en_US",
			lang = "en";
			
		if (use_locale == "ko_KR")
		{
			lang = "ko";
		}
		
		me._lang = lang;
			
		req.init(me, 
			{
				ack: "manual",
				payload: {lang: lang},
				mbody: {option: "menu"}
			}, me, function(jdoc) {
				var tf = me.down("[name=menus]"),
					mroot = {
						name: "Root",
						children: jdoc.children || []
					};
				// this._r3/*formatData*/(tval, mroot, "");
				tf.store.setRootNode(mroot);
				tf.store.getRootNode().expand(true);
			}, false);
		req.send();	
	},
	
	_save_menus: function() {
		var me = this,
			tgrid = me.down("[name=menus]"),
			root = tgrid.getRootNode(),
			dt = {
				name: "root",
				children: []
			},
			lang = me._lang,
			req = new IG$._rpc$();
		
		me._upd_menus(root.childNodes, dt.children);
		
		req.init(me, 
			{
				ack: "manual",
				payload: {option: "save_menu", lang: lang},
				mbody: dt,
				meta_type: "object"
			}, me, function(jdoc) {
			}
		);
		
		req.send();
	},
	
	_upd_menus: function(rnode, children) {
		var me = this;
		
		if (rnode)
		{
			$.each(rnode, function(i, child) {
				var p = {
					name: child.get("name"),
					dockey: child.get("dockey"),
					visible: child.get("visible")
				};
				children.push(p);
				
				if (child.childNodes && child.childNodes.length)
				{
					p.children = [];
					me._upd_menus(child.childNodes, p.children);
				}
			});
		}
	},
	
	save_content: function() {
		var me = this,
			editor = me._note_editor,
			cnt,
			req = new IG$._rpc$(),
			lang = me._lang;
		
		if (me._sel_content)
		{
			cnt = {
				html: editor.summernote("code")
			};
			
			req.init(me, 
				{
					ack: "manual",
					payload: {
						uid: me._sel_content.get("dockey"),
						lang: lang,
						option: "save"
					},
					mbody: cnt,
					meta_type: "object"
				}, me, function(jdoc) {
		
				}, null);
			req.send();
		}
	},
	
	_load_content: function() {
		var me = this,
			note_editor = me._note_editor;
			
		if (me._sel_content)
		{
			var me = this,
				req = new IG$._rpc$(),
				lang = me._lang;
			
			req.init(me, 
				{
					ack: "manual",
					payload: {lang: lang, uid: me._sel_content.get("dockey")},
					mbody: {option: "get"}
				}, me, function(jdoc) {
					if (jdoc.html)
					{
						note_editor.summernote("code", jdoc.html);
					}
					else
					{
						note_editor.summernote("code", "");
					}
				}, false);
			req.send();	
		}
	},
	
	initComponent: function() {
		var me = this;
		
		$s.apply(me, {
			items: [
				{
					xtype: "panel",
					layout: "border",
					split: true,
					items: [
						{
							xtype: "panel",
							region: "north",
							width: 350,
							height: 420,
							layout: "fit",
							title: "Menu Structures",
							items: [
								{
									xtype: "treepanel",
									name: "menus",
									enableDragDrop: true,
									enableDD: true,
									useArrows: true,
									ddGroup: "_I$RD_G_",
									// hidden: true,
									store: {
										xtype: "treestore"
									},
									columns: [
										{
											xtype: "treecolumn",
											text: IRm$.r1("B_NAME"),
											dataIndex: "name",
											flex: 1,
											editor: {
												xtype: "textfield"
											}
										},
										{
											xtype: "gridcolumn",
											dataIndex: "dockey",
											text: "KEY",	
											editor: {
												xtype: "textfield"
											}
										},
										{
											xtype: "checkcolumn",
											dataIndex: "visible",
											text: "Visible",
											width: 40
										},
										{
											xtype: "actioncolumn",
											width: 60,
											items: [
												{
													iconCls: "icon-grid-delete",
													tooltip: IRm$.r1("L_REMOVE"),
													handler: function(grid, rowIndex, colIndex, rec) {
														rec.parentNode.removeChild(rec);
													},
													scope: this
												}
											]
										}
									],
									tbar: [
										{
											text: "Save",
											name: "btn_save",
											iconCls: 'icon-toolbar-save',
											handler: function() {
												var me = this;
												me._save_menus();
											},
											scope: this
										},
										{
											text: "Add",
											handler: function() {
												var me = this,
													tgrid = me.down("[name=menus]"),
													sel = tgrid.getSelectionModel().selected,
													rec,
													n = 0;
												
												if (sel && sel.length)
												{
													rec = sel.items[0];
													
													if (rec.childNodes && rec.childNodes.length)
													{
														n = rec.childNodes.length;
													}
													rec.insertChild(n, {});
												}
											},
											scope: this
										}
									],
									listeners: {
										cellclick: function(tobj, td, cellIndex, record, rowIndex, e) {
											var me = this;
											if (cellIndex == 0 && record.childNodes && record.childNodes.length)
											{
												record.expand(true);
											}
											me._sel_content = record;
											me._load_content();
										},
										scope: this
									}
								}
							]
						},
						{
							xtype: "panel",
							region: "center",
							flex: 1,
							title: "Manual Content Editor",
							layout: "fit",
							items: [
								{
									name: "t_note_editor",
									html: "<div id='manual_editor_main'></div>"
								}
							],
							tbar: [
								{
									text: "Save",
									handler: function() {
										this.save_content();
									},
									scope: this
								},
								{
									text: "Refresh",
									handler: function() {
										if (me._sel_content)
										{
											this._load_content();
										}
									},
									scope: this
								}
							]
						}
					]
				}
			]
		});
		IG$.MgrManual.superclass.initComponent.call(this);
	},
	listeners: {
		afterrender: function() {
			var me = this;
			me._init_app();
		}
	}
});
