IG$.mg_meta = $s.extend(IG$.v$pnl, {
    layout: "fit",
    iconCls: "icon-ing-docdef",
    _module: "com.amplix.migration.upgrade_v3.MetaMigration",
    autoScroll: true,
    initapp: function() {
        var me = this,
            d_file = me.down("[name=d_file]"),
            d_file_el = d_file.el.dom,
            m_fileupload = $(".m_fileupload", d_file_el),
            
            d_upfiles = me.down("[name=d_upfiles]");
        
        m_fileupload.empty();
        
        fileupload = $("<input type='file' name='files[]' data-url='upload' multiple></input>").appendTo(m_fileupload);
        dropzone = $("<div class='filedropzone fade well'>Drop files here</div>").appendTo(m_fileupload);
        d_progress = $("<div class='file-progress'><div class='bar' style='width: 0%;'></div></div>").appendTo(m_fileupload);
        
        fileupload.fileupload({
            url: ig$.servlet + "/upload",
            dataType: "json",
            formData: {
				_mts_: IG$._g$a || ""
			},
            done: function (e, data) {
                var jdoc = data.result || {errorcode: "0xffff", errormsg: "Server incorrect responding"},
                    errcode = jdoc.errorcode,
                    tnodes,
                    i, dp = [], p, rec;
                
                if (errcode)
                {
                    IG$.ShowErrorMessage(jdoc, me, null);
                }
                else
                {
                    if (jdoc.result)
                    {
                        tnodes = jdoc.result.length ? jdoc.result : [jdoc.result];
                        
                        for (i=0; i < tnodes.length; i++)
                        {
                            p = tnodes[i];
                            dp.push(p);
                        }
                    }
                }
                
                if (dp && dp.length)
                {
                    d_upfiles.show();
                    
                    for (i=0; i < dp.length; i++)
                    {
                        rec = d_upfiles.store.add(dp[i]);
                        d_upfiles.selModel.select(rec, true);
                    }
                }
            },
            progressall: function (e, data) {
                var progress = parseInt(data.loaded / data.total * 100, 10),
                    bar = $(".bar", d_progress);
                bar.css(
                    "width",
                    progress + "%"
                );
                bar.text("Loaded " + data.loaded + " / " + data.total);
            },
     
            dropZone: dropzone
        }).bind('fileuploadsubmit', function (e, data) {
        });
        
        fileupload.fileupload("option", "url", ig$.servlet + "/upload");
    },
    
    toolbarHandler: function(cmd) {
        var me = this,
            d_upfiles = me.down("[name=d_upfiles]"),
			d_doc = me.down("[name=d_doc]"),
            i, flists = [];
        
        switch (cmd)
        {
        case "cmd_c_files":
            for (i=0; i < d_upfiles.store.data.items.length; i++)
            {
            	flists.push(d_upfiles.store.data.items[i]);
            }
            
            me.file_lists = flists;
            
            if (!me.checkRequired())
            	return;
            
            if (flists.length)
            {
                me.setLoading(true);
				me.$imp_cnts = [];
                me.migrate_import();
            }
            break;
		case "cmd_c_doc":
			var sel = d_doc.getSelectionModel().selected;
			
            me.doc_lists = flists;
            
            if (!me.checkRequired())
            	return;
            
            if (sel && sel.length)
            {
                me.setLoading(true);
                me.execute_upgrade();
            }
            break;
        }
    },
    
    checkRequired: function() {
    	var me = this,
    		r = true;
        
        return r;
    },
    
    migrate_import: function() {
        var me = this,
            d_upfiles = me.down("[name=d_upfiles]");
        
        if (me.file_lists && me.file_lists.length)
        {
            var rec = me.file_lists.splice(0, 1)[0],
                req = new IG$._rpc$();
            
            req.stoploading = false;
            
            req.init(me, 
                {
                    ack: "migration",
                    payload: {module: me._module},
                    mbody: {
                        option: "migrate_import",
                        items: [rec.data]
                    },
                    meta_type: "object"
                }, me, function(jdoc) {
					var me = this;
					
					me.$imp_cnts = me.$imp_cnts.concat(jdoc);
                    me.migrate_import();
                }
            );
            
            req.send();
        }
        else
        {
            me.setLoading(false);

			var d_doc = me.down("[name=d_doc]"),
				status_desc = {
					5: "Missing",
					9: "Exists",
					10: "Node Exists"
				},
				i;
				
			for (i=0; i < me.$imp_cnts.length; i++)
			{
				me.$imp_cnts[i].status_desc = status_desc[me.$imp_cnts[i].status];
			}
			
			d_doc.store.loadData(me.$imp_cnts);
        }
    },

	execute_upgrade: function() {
		var i,
			me = this,
			d_doc = me.down("[name=d_doc]"),
			d_upfiles = me.down("[name=d_upfiles]"),
			sel = d_doc.getSelectionModel().selected,
			rec,
			files = [],
			selections = [];
			
		for (i=0; i < sel.length; i++)
        {
			rec = sel.items[i];
         	selections.push(rec.get("uid"));   
        }

		for (i=0; i < d_upfiles.store.data.items.length; i++)
        {
            rec = d_upfiles.store.data.items[i];
            files.push(rec.data);
        }

		var rec = me.file_lists.splice(0, 1)[0],
            req = new IG$._rpc$();
        
        req.stoploading = false;
        
        req.init(me, 
            {
                ack: "migration",
                payload: {module: me._module},
                mbody: {
                    option: "execute_migrate",
                    files: files,
					selections: selections
                },
                meta_type: "object"
            }, me, function(jdoc) {
            }
        );
        
        req.send();
	},
	
    initComponent: function(){
        var me = this;
        
        $s.apply(me, {
            items: [
                {
                    xtype: "container",
                    padding: ig$.padding,
                    layout: {
                        type: "vbox",
                        align: "stretch"
                    },
                    items: [
                        {
                            xtype: "fieldset",
                            flex: 1,
                            layout: {
                                type: "vbox",
                                align: "stretch"
                            },
                            title: "Meta file upload",
                            items: [
                                {
                                    html: "<div class='m_fileupload'></div>",
                                    name: "d_file",
                                    height: 120,
                                    bodyPadding: 10
                                },
                                {
                                    name: "d_upfiles",
                                    xtype: "gridpanel",
                                    height: 320,
                                    hidden: true,
                                    selType: "checkboxmodel",
                                    selModel:  {
                                        mode: "MULTI"
                                    },
                                    store: {
                                        fields: [
                                            "uid", "filename", "fileext"
                                        ]
                                    },
                                    columns: [
                                        {
                                            text: IRm$.r1("L_FILENAME"),
                                            dataIndex: "filename_org",
                                            flex: 1
                                        },
                                        {
                                            text: IRm$.r1("L_FILEEXT"),
                                            dataIndex: "fileext",
                                            flex: 1
                                        },
                                        {
                                            xtype: 'actioncolumn',
                                            width: 20,
                                            items: [
                                                {
                                                    iconCls: "icon-grid-delete",
                                                    tooltip: 'Delete item',
                                                    handler: function (grid, rowIndex, colIndex) {
                                                        var rec = grid.store.getAt(rowIndex);
                                                        
                                                        grid.store.remove(rec);
                                                    }
                                                }
                                            ]
                                        }
                                    ],
                                    bbar: [
                                        "->",
                                        {
                                            xtype: "button",
                                            text: "Load Meta Informations",
                                            handler: function() {
                                                this.toolbarHandler("cmd_c_files");
                                            },
                                            scope: this
                                        },
                                        {
                                            xtype: "button",
                                            text: "Cancel All Files",
                                            handler: function() {
                                                var me = this,
                                                    d_upfiles = me.down("[name=d_upfiles]");
                                                    
                                                d_upfiles.store.removeAll();
                                                d_upfiles.hide();
                                                
                                                me.down("[name=d_file]").hide();
                                            },
                                            scope: this
                                        }
                                    ]
                                }
                            ]
                        },
						{
                            xtype: "fieldset",
                            flex: 1,
                            layout: {
                                type: "vbox",
                                align: "stretch"
                            },
                            title: "Backup Items",
                            items: [
                                {
                                    name: "d_doc",
                                    xtype: "gridpanel",
                                    height: 500,
                                    selType: "checkboxmodel",
                                    selModel:  {
                                        mode: "MULTI"
                                    },
                                    store: {
                                        fields: [
                                            "name", "id", "description"
                                        ]
                                    },
                                    columns: [
                                        {
                                            text: IRm$.r1("B_NAME"),
                                            dataIndex: "name",
											minWidth: 180,
                                            flex: 1
                                        },
										{
											text: "Node Path",
											dataIndex: "nodepath_name",
											minWidth: 300,
											flex: 1
										},
										{
											text: "Status",
											dataIndex: "status_desc",
											width: 80
										},
                                        {
                                            text: IRm$.r1("B_DESC"),
                                            dataIndex: "description",
											minWidth: 100,
                                            flex: 1
                                        }
                                    ],
                                    tbar: [
                                        "->",
                                        {
                                            xtype: "button",
                                            text: "Migrate Selected Items",
                                            handler: function() {
                                                this.toolbarHandler("cmd_c_doc");
                                            },
                                            scope: this
                                        }
                                    ]
                                }
                            ]
                        }
                    ]
                }
            ]
        })
        IG$.mg_meta.superclass.initComponent.call(this);
    },
    
    listeners: {
        afterrender: function(tobj) {
            tobj.initapp();
        }
    }
});